import React, { FC, useState, useEffect } from 'react';
import { Select, Spin, Empty } from 'antd';
import { searchDramaContent, searchTopN } from './services';

const { Option } = Select;

let timeout;

function fetch({value, type}, callback: any) {
  if (timeout) {
    clearTimeout(timeout);
    timeout = null;
  }
  function fake() {
    let searchParams;

    if (type) {
      searchParams = { title: value, type };
    } else {
      searchParams = { title: value };
    }

    searchDramaContent(searchParams).then((res: any) => {
      const list = res?.data?.list || [];

      callback(list);
    });
  }

  timeout = setTimeout(fake, 300);
}

function fetchInit(value: string, callback: any) {
  function fake() {
    searchTopN({ type: parseInt(value), topN: 50 }).then((res: any) => {
      const list = res?.data || [];

      callback(list);
    });
  }
  if (value) {
    if (timeout) {
      clearTimeout(timeout);
      timeout = null;
    }
    timeout = setTimeout(fake, 300);
  }
}

interface Props {
  value?: string;
  onChange?: (time?: string) => void;
  width?: number|string;
}

const DramaContentSearch: FC<Props> = (props: any) => {
  const { value, onChange, width, type } = props;
  const [list, setlist] = useState<any[]>([]);

  useEffect(() => {
    fetchInit(type, (resList: any[]) => setlist(resList));
  }, [type]);

  const handleSearch = (inputVal: string) => {
    if (inputVal) {
      fetch({value: inputVal, type: parseInt(type)}, (resList: any[]) => setlist(resList));
    } else {
      fetchInit(type, (resList: any[]) => setlist(resList));
    }
  };

  const handleChange = (inputVal: string) => {
    const { value } = inputVal;
    const { listId } = JSON.parse(value);

    onChange({ ...inputVal, totalItem: value, value: listId });
  };

  return (
    <Select showSearch={ true }
      labelInValue={ true }
      value={ value }
      style={{ width: width || '100%' }}
      placeholder="输入名称搜索（模糊搜索）"
      defaultActiveFirstOption={ true }
      showArrow={ false }
      filterOption={ false }
      onSearch={ handleSearch }
      onChange={ handleChange }
      notFoundContent={ <Empty /> }
    >
      {list.map((d: any) => (<Option value={ JSON.stringify(d) } key={ d.listId }>{d.title}</Option>))}
    </Select>
  );
};

export default DramaContentSearch;