import React, { FC, useState } from 'react';
import { Select, Spin, Empty } from 'antd';
import { searchDramaContent } from './services';

const { Option } = Select;

let timeout;

function fetch(value: string, callback: any) {
  if (timeout) {
    clearTimeout(timeout);
    timeout = null;
  }
  function fake() {
    searchDramaContent({ title: value, onlineStatus: '1' }).then((res: any) => {
      const list = res?.data?.list || [];

      callback(list);
    });
  }

  timeout = setTimeout(fake, 300);
}

interface Props {
  value?: any;
  onChange?: (time?: string) => void;
  width?: number|string;
}

const DramaContentSearch: FC<Props> = (props: any) => {
  const { value, onChange, width } = props;
  const [list, setlist] = useState<any[]>([]);

  const handleSearch = (inputVal: string) => {
    if (inputVal) {
      fetch(inputVal, (resList: any[]) => setlist(resList));
    } else {
      setlist([]);
    }
  };

  const handleChange = (inputVal: string) => {
    const { value } = inputVal;
    const { dramaId } = JSON.parse(value);

    onChange({ ...inputVal, totalItem: value, value: dramaId });
  };

  return (
    <Select showSearch={ true }
      labelInValue={ true }
      value={ value }
      style={{ width: width || '100%' }}
      placeholder="输入名称搜索（模糊搜索）"
      defaultActiveFirstOption={ true }
      showArrow={ false }
      filterOption={ false }
      onSearch={ handleSearch }
      onChange={ handleChange }
      notFoundContent={ <Empty /> }
    >
      {list.map((d: any) => (<Option value={ JSON.stringify(d) } key={ d.dramaId }>{d.title}</Option>))}
    </Select>
  );
};

export default DramaContentSearch;