// 获取topN的官方数据或者用户榜单数据
export async function searchTopN(params: { topN: number; type: number }) {
  return await request(
    `${requestBasePath}/wingvate/wind-vane-backend/public/list/query/getTopNList`,
    { method: 'POST', body: params }
  );
}
